  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
}
html{
  font-size: 2.5vh;
}
body{
  font-family: 'Mulish', sans-serif;


}
#main{
  width:100%;
  height:100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}
#main_container{
  display:flex;
  width:120.247vh;
  height:85.041vh;  


}
#current_weather{
  background-color: white;
  width: 29.45%;
  height: 100%;
  border-radius: 12.65% 0 0 12.65% / 5.26% 0 0 5.26%;

  


}
#currentWeather_margin{
  margin:11.3%;
  height: 90.5%;
