  color:black;
  font-size: 1.7vh;

}

#faSearch{
  height:auto;
  width:7.589%;
  margin-left: 2%;
  margin-right: 4%;
  
 
}
#faMapMarkerLink{
margin-left: 4%;
margin-top: 2%;
}
#faMapMarkerAlt{
  height: auto;
  width: 1.6vh;
  border: 0.6vh solid #F6F6F8;
  border-radius: 10px;

  vertical-align: top;

  
}
#faMapMarkerAlt:hover{
  transform: scale(1.1);
} 

