#searchResults{
  display: none;
  position: absolute;
  background-color: white;
  border-radius: 0.8vh;
  max-height: 20vh;
  overflow: auto;
  width: 27.4vh;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
   0 2.8px 2.2px rgba(0, 0, 0, 0.048),
    0 2.8px 2.2px rgba(0, 0, 0, 0.06),
     0 2.8px 2.2px rgba(0, 0, 0, 0.072),
      0 2.8px 2.2px rgba(0, 0, 0, 0.086), 
      0 -2.8px 2.2px rgba(0, 0, 0, 0.034),
       0 -2.8px 2.2px rgba(0, 0, 0, 0.048);
}
#searchList{
  list-style-type: none;
   padding:0;
}
.searchListItems{
  font-size: 3vh;
  line-height: 250%;
  padding-left: 10%;
}
#searchList li:hover{
  background-color: #D6D7DA;
}

.searchListLinks{
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  color:black;
  height:100%;
  width:100%;
}
#main_image{
  
   width:85%;
   height: auto;
}
#line{
  border-bottom: 0.16vh solid #EFEFF1;
}
.temperature{
  font-size: 3rem;
    margin-bottom: 7%;
