    flex-direction: column;
    justify-content: space-between
 
}
#header{
  width:100%;
  display: flex;
  justify-content: space-between;

 

}
#today_week{
  display: flex;
 align-items:center;
 font-weight: 900;
 font-size: 0.7rem;
}
#today{
