}
.circle{
  margin: auto;
  width: 4vh;
  height: 4vh;
  border-radius: 50%;
  display: inline-block;
  margin-right: -2.88vh;
  vertical-align: middle;
}
#celsius{
  color:white;

  text-decoration: none;
  margin-right: 1.6vh;
}
#celsius .circle{
  background-color:#1A1A1A;
}

#fahrenheight{
  color:black;
  
     text-decoration: none;
}
